// _content.scss
.columns-wrapper {
	@media screen and (min-width: $break-tabletSmall) {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;
	}
	@media screen and (min-width: $break-desktopSmall) {
		margin-left: -20px;
		margin-right: -20px;
	}
	.column {
		@media screen and (min-width: $break-tabletSmall) {
			width: calc(50% - 20px);
			margin: 0 10px;
		}
		@media screen and (min-width: $break-desktopSmall) {
			width: calc(33.33% - 40px);			
			margin: 0 20px;
		}
	}
}
#column-3 {
	@media screen and (max-width: $break-tabletSmall - 1px) {
		display: none;
	}
}
#grid {
	margin-bottom: 20px;
	@media screen and (min-width: $break-desktopSmall) {
		margin-bottom: 40px;
	}
	.item {
        break-inside: avoid;
        padding: 0 0 20px;
        @media screen and (min-width: $break-tabletSmall) {
        	padding: 0 0 20px;
        }
        @media screen and (min-width: $break-desktopSmall) {
        	padding: 20px 0;
        }
        .inner  {
			position: relative;
			box-shadow: 0 0 18px rgba(#000,.10);
			padding: 20px; 
			background: #fff;
	        @media screen and (min-width: $break-desktopSmall) {
	        	padding: 30px;
	        }
        }
		.toggle {
			position: absolute;
			right: 16px;	
			top: 6px;
			@media screen and (min-width: $break-tabletSmall) {
				top: 13px;				
			}
		}
		h3 {
			@include fontsize(20px);
			margin-bottom: 6px;
		}
		.options {
			display: none;
		}
		form {
			@media screen and (min-width: $break-tabletSmall) {
	        	display: flex;
	        	flex-direction: column;
	        	min-height: 145px;
			}
			@media screen and (min-width: 1350px) {
	        	min-height: 166px;
			}
			p {
				margin-bottom: 16px;
				line-height: 1.75;
				@media screen and (min-width: $break-tabletSmall) {
					margin-bottom: 21px;
				}
				@media screen and (min-width: $break-desktopSmall) {
					margin-bottom: 14px;
				}
			}
		}
		.footer {
			@include clearfix();
			margin-top: auto;
			.update-subscription {
				display: block;
				margin: 0 auto 22px;
				@media screen and (min-width: $break-tabletSmall) {
					float: left;
					margin: 0;	
				}
				@media screen and (min-width: $break-desktopSmall) {
					float: none;
					display: block;
					margin: 0 auto 22px;
					max-width: 100%;
				}
				@media screen and (min-width: 1350px) {
					float: left;
					margin: 0;	
					max-width: 187px;	
				}
			}
			.preview {
				display: block;
				margin: 0 auto;
				min-height: 44px;
				@media screen and (min-width: $break-tabletSmall) {
					float: right;	
				}
				@media screen and (min-width: $break-desktopSmall) {
					float: none;
					display: block;
					margin: 0 auto;
				}
				@media screen and (min-width: 1350px) {
					float: right;	
				}
			}
		}
	}
}
.update-subscription {
	@include fontsize(16px);
	text-align: center;
	width: 100%;
	padding: 14px 10px;
	border: none;
	cursor: pointer;
	transition: 0.3s;
	min-height: 46px;
	@media screen and (min-width: $break-tabletSmall) {
		max-width: 187px;
	}
	&:first-letter {
	  text-transform: capitalize
	}
	&[value="subscribe-to-all"],
	&[value="subscribe"] {
		background: $color-red;
		color: #fff;
		&:hover {
			background: #000;
		}
	}
	&[value="unsubscribe-from-all"],
	&[value="unsubscribe"] {
		font-weight: 600;
		background: $color-tan-3;
		&:hover {
			background: $color-grey-8;
		}
	}
}
section.user-data {
	.container{
		@media screen and (min-width: $break-desktopSmall) {
			position: relative;
		}
	}
}
.contact-info {
	margin-bottom: 25px;
	@media screen and (min-width: $break-desktopSmall) {
		position: absolute;
		right: 0;
		width: 387px;
		padding-left: 75px;
		top: 7px;
	}
	h2 {
		@include fontsize(22px);
		line-height: 1.3;
		margin-bottom: 10px;
		@media screen and (min-width: $break-tabletSmall) {
			@include fontsize(30px);
		}
	}
	.assists {
		@include fontsize(16px);
		margin-bottom: 13px;
		.edit-details {
			font-weight: bold;
			margin-right: 35px;
			text-decoration: none;
			border-bottom: 2px solid $color-red;	
			display: inline-block;
		}
		.not-you {
			font-weight: 100;
			color: $color-red;	
			display: inline-block;
			&:hover {
				color: #fff;
			}
		}
	}
	.details {
		@include fontsize(14px);
		@media screen and (min-width: $break-tabletSmall) {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
		}
		@media screen and (min-width: $break-desktopSmall) {
			flex-direction: column;
		}
		p {
			margin-bottom: .7rem; 
			@media screen and (min-width: $break-tabletSmall) {
				width: 33%;
				padding-right: 25px;
			}
			@media screen and (min-width: $break-desktopSmall) {
				width: 100%;
				padding-right: 0;
			}
		}
		.label {
			margin-right: 15px;
			display: inline-block;
		}
		.data {
			@include fontsize(15px);
			font-weight: bold;
		}
	}
}
.location-interest {
	.inner {
		background: center center url('../images/accent-bg.jpg');
		background-size: cover;
		padding: 15px 15px 30px;
		margin-bottom: 28px;
		@media screen and (min-width: $break-desktopSmall) {
			max-width: calc(100% - 378px);
			padding: 20px 30px 30px;
		}
		h2 {
			@include fontsize(22px);
			font-weight: 600;
			margin-bottom: 6px;
			line-height: 1.7;
			@media screen and (min-width: $break-tabletSmall) {
				@include fontsize(30px);
				font-weight: 700;
			}
		}
		label {
			display: block;
			margin-bottom: 12px;
		}
		select {
			max-width: 380px;
		}
	}
	.location-list {
		margin-bottom: 25px;
		@media screen and (min-width: $break-desktopSmall) {
			max-width: calc(100% - 378px);
			margin-bottom: 50px;
		}
		button {
			@include fontsize(16px);
			color: #000;
			display: inline-block;
			border-radius: 200px;
			padding: 4px 50px 6px 15px;
			margin-right: 18px;
			border: none;
			cursor: pointer;
			position: relative;
			min-width: 100px;
			text-align: left;
			min-height: 44px;
			margin-bottom: 10px;
			&:hover {
				.close {
					color: #000;
				}
			}
			.close {
				color: $color-border-2;
				pointer-events: none;
				position: absolute;
				right: 14px;
				top: 16px;
				width: 12px;
				height: 12px;
			}
		}
	}
	#location-notice {
		display: block;
		margin-top: 7px;
	}
}
.message {
	margin-bottom: 29px;
	@media screen and (min-width: $break-desktopSmall) {
		margin-bottom: 36px;
	}
	.preview {
		@include fontsize(14px);
		margin-top: 21px;
		display: inline-block;
		border-bottom: 1px solid transparent;
		&:hover,
		&:active {
			color: #000;
			border: 0;
			background: transparent;
			border-bottom: 1px solid $color-red;
		}
		@media screen and (min-width: $break-desktopSmall) {
			margin-top: 16px;
			@include fontsize(16px);
		}
	}
}
section.communications {
	h2 {
		@include fontsize(22px);
		font-weight: 700;
		margin-bottom: 20px;
		@media screen and (min-width: $break-tabletSmall) {
			@include fontsize(30px);
		}
	}
}
section.topics-section {
	@media screen and (min-width: $break-desktopSmall) {
		margin-bottom: 40px;
	}
	.container {
		form {
			max-width: 100%;
		}
	}
	.inner {
		@media screen and (min-width: $break-desktopSmall) {
			margin-right: auto;
			border-top: 1px solid $color-border;
			max-width: 1060px;
			padding-top: 40px;
		}		
	}
	h2 {
		@include fontsize(22px);
		font-weight: 700;
		margin-bottom: 20px;
		@media screen and (min-width: $break-tabletSmall) {
			@include fontsize(30px);
		}
	}
	legend {
		@include fontsize(18px);
		margin-bottom: 20px;
	}
	.check-all {
		margin-bottom: 10px;
		label {
			font-weight: 700;
		}
	}
	.topics-wrapper {
		@media screen and (min-width: $break-tabletSmall) {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
		}
		@media screen and (min-width: $break-desktopSmall) {
			margin-bottom: 20px;
		}
		.checkbox {	
			align-self: flex-start;
			label {
				font-weight: 400;	
				display: flex;
				flex-direction: column;
				justify-content: center;
				&:before {
					top: 50%;
					transform: translate(0,-50%);
				}
				&:after {
					top: 50%;
					transform: translate(0,-75%) rotate(-50deg);
				}
			}
		}
	}
	.checkbox {
		margin-bottom: 5px;
		@media screen and (min-width: $break-tabletSmall) {
			width: 50%;
			padding-right: 25px;
		}
		@media screen and (min-width: $break-desktopSmall) {
			width: 33.33%;
		}
		label {
			@include fontsize(18px);
		}
	}
	.button {
		@include fontsize(18px);
		font-size: 1rem;
		text-align: center;
		width: 100%;
		max-width: 186px;
		padding: 14px 10px;
		border: none;
		cursor: pointer;
		transition: .3s;
		min-height: 46px;
		background: #e30613;
		color: #fff;
		&:hover {
			background-color: #000;
		}
	}
}
section.disclosures {
	margin-bottom: 20px;
	margin-top: 20px;
	@media screen and (min-width: $break-desktopSmall) {
		margin-bottom: 60px;
	}
	p {
		max-width: 600px;
		line-height: 1.5;	
	}
}
body.opt-down {
	#grid {
		max-width: 800px;
		columns: unset;	
		margin-bottom: 59px;
		.item {
			h2 {
				margin-bottom: 16px;
				.title {
					font-weight: 600;
				}
			}
			.footer {
				.update-subscription {
					float: none;
					margin-right: 35px;
					display: block;
					margin: 0 35px 22px 0;
					@media screen and (min-width: $break-tabletSmall) {
						max-width: 187px !important; 
						float: left;
					}
				}
				.preview {
					display: block;
					margin: 0 auto;
					@media screen and (min-width: $break-tabletSmall) {
						float: left;
						display: inline-block;
					}
				}
			}
		}
	}
	.message {
		margin-bottom: 78px;
		&:last-of-type {
			margin-bottom: 115px;
		}
		h2 {
			margin-bottom: 10px;
		}
		.preview {
			margin-top: 8px;
		}
	}
}
.slider-wrapper {
	overflow: hidden;
	.slick-slider {
		overflow: hidden;
	}
}
#preview-modal {
	max-width: 740px;
	padding: 20px;
	.inner {
		background: #fff;
		padding: 20px 20px 20px;
		max-height: calc(100vh - 60px);
		box-shadow: 0 0 18px rgba(#000, .16);
		@media screen and (min-width: $break-desktopSmall) {
			padding: 22px 30px 30px;
		}		
	}
	.close-button {
		@include clearButton();
		position: absolute;
		right: 39px;
		top: 34px;
		min-height: 45px;
		cursor: pointer;
		font-weight: 600;
		font-family: $font-global;
		@media screen and (min-width: $break-desktopSmall) {
			right: 51px;
		}
	 	span.label {
			padding-right: 19px;
			display: inline-block;
			position: relative;
			z-index: 2;	 		
	 	}
	 	svg {	 		
			width: 14px;
			height: 14px;
			display: inline-block;
			vertical-align: middle;
			margin-left: 7px;
			position: absolute;
			right: 0;
			top: 16px;
			color: $color-red;
			z-index: 1;
	 	}
	}
	.header {
		margin-bottom: 20px;
	}
	h2 {
		color: #000;
	}
	.wrapper {
		max-height: calc(100vh - 250px);
		overflow-y: scroll;
		border: 1px solid $color-grey-7;
		&::-webkit-scrollbar {
		  width: 7px;
		  height: 0px;
		}
		&::-webkit-scrollbar-button {
		  width: 7px;
		  height: 0px;
		}
		&::-webkit-scrollbar-thumb {
		  background: #dbd6c7;
		  border: 0px none #ffffff;
		  border-radius: 0px;
		}
		&::-webkit-scrollbar-thumb:hover {
		  background: #d0cab7;
		}
		&::-webkit-scrollbar-thumb:active {
		  background: #d0cab7;
		}
		&::-webkit-scrollbar-track {
		  background: #f6f5f1;
		  border: 0px none #ffffff;
		  border-radius: 0px;
		}
		&::-webkit-scrollbar-track:hover {
		  background: #f6f5f1;
		}
		&::-webkit-scrollbar-track:active {
		  background: #f6f5f1;
		}
		&::-webkit-scrollbar-corner {
		  background: transparent;
		}
	}
	img {
		display: block;
		margin: 0 auto;
		width: 100%;
	}
}
body.confirmation-page {
	#masthead {
		@media screen and (min-width: $break-desktopSmall) {
			margin-bottom: 136px;
		}
	}
	main {
		min-height: calc(100vh - 302px); //Header Height + Footer Height = 399
		@media screen and (min-width: $break-mobileLarge) {
			min-height: calc(100vh - 276px);
		}
		@media screen and (min-width: $break-tabletSmall) {
			min-height: calc(100vh - 306px); 
		}
		@media screen and (min-width: $break-desktopSmall) {
			min-height: calc(100vh - 399px); 
		}
	}
}
section.confirmation-message {
	max-width: 900px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	.user {
		margin-bottom: 24px;
		#user {
			@include fontsize(16px);
		}
	}
	.message {
		margin-bottom: 47px;
	}
	h2 {
		margin-bottom: 37px;
	}
	h4 {
		color: $color-red;
		margin-bottom: 20px;
	}
	h3 {
		line-height: 1.8;
	}
}
section.my-info {
	margin-bottom: 60px;
	@media screen and (min-width: $break-tabletSmall) {
		margin-bottom: 133px;
	}
}
