/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    overflow: hidden;
    opacity: 0;
    transition: opacity 3s 4s ease-out;
    &.slick-initialized {
        opacity: 1;
    }
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    opacity: 0;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    .content {
        margin-left: 5px;
    }
    &.slick-current {
        opacity: 1;
        transition: opacity 0.3s ease-out;
    }
    &.slick-active .content {
        margin-left: 0;
    }

    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
#slider-arrows {
    margin-top: 20px;
    text-align: right;
    .slick-next {
        margin-left: 20px;
        svg {
            left: 19px;
        }
    }
    .slick-prev {
        svg {
            left: 17px;
        }
    }
    .slick-arrow {
        position: relative;
        top: 15px;
        background: $color-red;
        border: 0;
        width: 46px;
        height: 46px;
        padding: 0;
        cursor: pointer;
        transition: 0.3s;
        &:hover,
        &:focus {
            background: #000;
        }
        svg {
            position: absolute;
            top: 15px;
            fill: #fff;
        }
    }
}
.slick-arrow.slick-hidden {
    display: none;
}