body {
	@include fontsize(14px);
	color: #000;	
	font-family: $font-global;
	line-height: 1.75;
	overflow-x: hidden;
	position: relative;
	@media screen and (min-width: $break-desktopSmall) {
		@include fontsize(16px);
	}
}
 
h1,
.h1 {
	@include fontsize(32px); 
	font-weight: 700;
	line-height: 1;	
	margin-bottom: 20px;
	@media screen and (min-width: $break-tabletSmall) {
		@include fontsize(72px);	
		margin-bottom: 21px;
	}
	@media screen and (min-width: $break-desktopSmall) {
		margin-bottom: 37px;		
	}
}

h2,
.h2 {
	@include fontsize(32px);
	line-height: 1.5;
	font-weight: 600;
	@media screen and (min-width: $break-desktopSmall) {
		@include fontsize(72px);
	}
}

h3,
.h3 {
	@include fontsize(32px);
	line-height: 1.33;
	font-weight: 600;
	margin-bottom: 20px;
	@media screen and (min-width: $break-tabletSmall) {
		@include fontsize(56px);
	}
}

h4,
.h4 {
	@include fontsize(24px);
	line-height: 1.33;
	font-weight: 600;
	@media screen and (min-width: $break-tabletSmall) {
		@include fontsize(32px);
		line-height: 1.25;
	}
}

h5,
.h5 {
	@include fontsize(18px);
	line-height: 1.33;
	font-weight: 300;
	@media screen and (min-width: $break-tabletSmall) {
		@include fontsize(24px);
	}
}

h6,
.h6 {
	@include fontsize(16px);
	line-height: 1.31;
	font-weight: 600;
	@media screen and (min-width: $break-tabletSmall) {
		@include fontsize(20px);
		line-height: 1.2;
	}
}
strong {
	font-weight: 600;
}
em {
	font-style: italic;
}
a {
	border-bottom: 1px solid $color-red;	
	text-decoration: none;
	font-weight: 600;
	color: #000;
	line-height: 1.3rem;
	&:hover,
	&:active {
		color: #fff;
		border-bottom: 1px solid transparent;
		background: $color-red;	
		text-decoration: none;
	}
}
ol {
	list-style-type: decimal;
	margin-bottom: 1rem;
	padding-left: 40px;
}
ul {
	margin-bottom: 1rem;
	list-style-type: disc;
	padding-left: 40px;
	li {
		position: relative; 
	}
}

pre,
font { 
	
}

p { 
	margin-bottom: 1rem;
	&.large {
		@include fontsize(18px);
		font-weight: 100;
		@media screen and (min-width: $break-desktopSmall) {
			@include fontsize(26px);
		}
	}
}