html:lang(ar), 
html:lang(ar_ae) {
	direction: rtl;
	main {
		background: left -210px bottom -70px no-repeat url(../images/background-paint.png);
		@media screen and (min-width: $break-tabletSmall) {
			background-position: left -300px bottom -70px;
		}
		@media screen and (min-width: $break-desktopSmall) {
			background-position: left -230px bottom -60px;
		}
	}
	select {
		padding: 8px 15px 8px 41px;
		background: left 14px top 17px no-repeat url(../images/down-arrow.png) #fff;
		background-size: 19px auto;
	}
	.contact-info {
		@media screen and (min-width: $break-desktopSmall) {
			position: absolute;
			right: auto;
			left: 0;
			width: 387px;
			padding-right: 75px;
			padding-left: unset;
		}
		.edit-details {
			margin-right: unset;
			margin-left: 35px;
		}
		.details .label {
			margin-right: unset;
			margin-left: 15px;
		}
	}
	.location-interest .location-list button {
		padding: 6px 15px 6px 50px;
		.close {
			right: unset;
			left: 14px;
		}
	}
	#grid .item .toggle {
		right: unset;
		left: 16px;	
	}
	.preview svg {
		transform: rotate(-180deg);
	}
	.checkbox {
		input[type=checkbox] {

		}
		label {	
			padding-left: unset;	
			padding-right: 46px;
			&:before {
				left: unset;
				right: 0;
			}
			&:after {
				left: unset;	
				right: 7px;
			}
		}
		.checkbox {
			margin-left: unset;
			margin-right: 38px;
		}
	}
	#slider-arrows {
		.slick-arrow svg {
			transform: rotate(-180deg);
		}
	}
	section.topics-section {
		.inner {
			@media screen and (min-width: $break-desktopSmall) {
				margin-right: unset;
				margin-left: auto;
			}		
		}
	}
	.my-info form .row {
		.field {
			@media screen and (min-width: $break-tabletSmall) {
				&:nth-of-type(1) {
					margin-right: unset;
					margin-left: 8px;
				}
				&:nth-of-type(2) {
					margin-left: unset;
					margin-right: 8px;
				}
			}
		}
	}
	#colophon .content p {
		float: left;
	}
}