.jll-ipl-search-bar__input-container {
    position: relative;
    width: 100%;
    max-width: 321px;
    .toggle-dropdown {
        background-color: transparent;
        border: none;
        width: 44px;
        height: 44px;
        text-align: left;
        position: relative;
        z-index: 4;
        flex-shrink: 0;
        padding: 0;
        justify-content: center;
        align-items: center;
        display: flex;
        &[aria-expanded="true"] .expand {
            transform: rotate(45deg);
        }
    }
    .dropdown {
        display: none;
        flex-direction: row;
        flex-wrap: wrap;
        > .dropdown {
            width: 100%;
        }
    }
    > .toggle-dropdown {
        background-color: #fff;
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        padding: 16px 0 16px 16px;
        border: 1px solid #000;
        &[aria-expanded="true"] .down-chevron {
            transform: rotate(-180deg);
        }
        .down-chevron {
            color: #E30613;
            width: 15px;
            height: 15px;
            margin-left: auto;
            margin: 0 20px 0 auto;
            transition: transform 0.3s ease-out;
        }
        .label {
            color: #707070;
            font-weight: 400;
            margin-right: auto;
            font-size: 1rem;
        }
    }
    .dropdown-container {
        max-height: 260px;
        position: absolute;
        z-index: 3;
        box-shadow: 0 0 10px 0 rgba(#000,.3);
        width: 100%;
        top: 100%;
        overflow: hidden;
        overflow-y: scroll;
        margin: 0;
        display: none;
        max-width: 100%;
        background-color: #fff;
        padding-top: 10px;
        padding-bottom: 5px;
        > .dropdown {
            display: flex;
            > legend + fieldset {
                padding-left: 0;
            }
        }
        > fieldset,
        > .checkbox {
            padding-left: 15px !important; //First level
        }
    }
    .expand {
        color: $color-red;
        width: 15px;
        height: 15px;
        transition: transform 0.3s ease-out;
    }
    fieldset {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        > legend + .checkbox {
            width: calc(100% - 44px); //dropdown spacing
        }
        &:not(.dropdown) {
            padding-left: 0;
        }
        fieldset {
            padding-left: 15px;
        }
    }
    .checkbox {
        width: 100%;
        label {
            margin-bottom: 0;
        }
    }
}

//RTL Languages
html:lang(ar), 
html:lang(ar_ae) {
    .jll-ipl-search-bar__input-container {
        >.toggle-dropdown {
            padding: 16px 16px 16px 0;
            .label {
                margin-right: initial;
                margin-left: auto;
            }
            .down-chevron {
                margin: 0 auto 0 20px;
            }
        }
        .dropdown-container {
            > .checkbox,
            > fieldset {
                padding-left: 0 !important;
                padding-right: 15px !important;
            }
        }
    }
    fieldset fieldset {
        padding-left: 0;
        padding-right: 15px;
    }
}