main {
	display: block;
	overflow-y: hidden;
	background: right -210px bottom -110px no-repeat url(../images/background-paint.png);
	background-size: 400px auto;

	@media screen and (min-width: $break-tabletSmall) {
		background-size: 600px auto;
		background-position: right -300px bottom -110px;
	}
	@media screen and (min-width: $break-desktopSmall) {
		background-size: 750px auto;		
		background-position: right -230px bottom -110px;
	}
}
#test-sort {
	display: block;
	position: fixed;
	background: white;
	width: 400px;
	padding: 20px;
	font-size: 16px;
	color: tomato;
	box-shadow: 0 0 14px rgba(#000,.25);
	z-index: 10000;
	top: 0;
	right: 0;
}
::focus {
	outline: 1px solid $focus;
}