.nav__country-selector {
    display: block;
    position: relative;
    @media screen and (min-width: $break-tabletSmall) {
        margin: 0 15px;
    }
    .toggle-dropdown {
        transition: 0.3s ease-out;
        &[aria-expanded="true"] svg {
            transform: rotate(180deg);
        }
    }
    &__activator {
        position: relative;
        height: 100%;
        background: none;
        border: none;
        display: flex;
        align-items: center;
        cursor: pointer;
        min-height: 44px;

        &--text {
            display: block;
        }

        &__carrot {
            display: flex;
            align-items: center;
            position: relative;
            height: 14px;
            width: 14px;
            margin: 5px;
            .down-chevron {
                transition: transform .3s ease-in-out;
                width: 14px;
                height: 14px;
                margin-left: 3px;
                color: #c9cacd;
                position: relative;
                top: 2px;
                color: #626468;
            }
        }
    }

    &__regions{
        transition: all .3s ease-in-out;
        display: block;
        background: #fff;
        margin: 0;
        border-radius: 2px;
        z-index: 1;
        overflow: hidden;
        list-style: none;
        display: none;
        @media screen and (min-width: $break-tabletSmall) {
            padding: 0 20px;
            position: absolute;
            right: 0;
            top: calc(100% + 15px);  
            box-shadow: 0 0 8px 0 rgba(0,0,0,.3);
            position: absolute;
            right: 0;
            top: calc(100% + 15px);    
        }
    }

    &__regions--list {
        display: flex;
        flex-wrap: wrap;
        background: #fff;
        padding: 0;
        margin: 0;
        list-style: none;
        padding-bottom: 20px;
    }

    &__region {
        @media screen and (max-width: $break-tabletSmall - 1px) {  
            width: 100%;
        }
        @media screen and (min-width: $break-tabletSmall) {
            padding: 0 20px;
            min-width: 190px;
        }
        &__label {
            color: #000;
            font-weight: 800;
            margin-bottom: 10px;
            display: block;
            @media screen and (max-width: $break-tabletSmall - 1px) {  
                display: none;
            }
        }
        .toggle-dropdown {
            width: 100%;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            background-color: transparent;
            border: none;
            min-height: 44px;
            display: flex;
            width: 100%;
            justify-content: space-between;
            color: #626468;
            border: none;
            font-weight: 400;   
            @include fontsize(16px);
            cursor: pointer;
            @media screen and (min-width: $break-tabletSmall) {
                display: none !important;
            }
            svg {
                width: 12px;
                height: 12px;
                color: #626468;
                transform-origin: center;
                transition: transform 0.3s ease-out;
            }
            .label {
                padding-top: 10px;
                color: #000000;
            }
            .nav__country-selector__activator__carrot {
                margin-left: auto;
                margin-top: 12px;
            }
        }
        &__countries {          
            top: 0;
            padding-left: 0;
            @media screen and (min-width: $break-tabletSmall) {
                columns: 1;   
                display: block !important;            
            }
            @media screen and (max-width: $break-tabletSmall - 1px) {  
                display: flex;
                flex-wrap: wrap;
                flex-direction: row; 
                display: none;               
            }
            &__country {
                list-style: none;
                @media screen and (min-width: $break-tabletSmall) {
                    font-size: .9rem;
                    white-space: nowrap;
                    display: flex;
                    align-items: center;
                    page-break-inside: avoid;                    
                }
                @media screen and (max-width: $break-tabletSmall - 1px) {    
                    width: 50%;
                    float: left;
                    color: #626468;
                    align-items: flex-start;
                    padding: 0.35rem 0;
                }
                &::before {
                    display: none;
                }
                &__flag {
                    @media screen and (max-width: $break-tabletSmall - 1px) {   
                        float: left; 
                    }
                }
                &__link {
                    color: #c9cacd;
                    border-bottom: 1px solid transparent;
                    margin: 0;
                    padding: 0;
                    font-weight: normal;
                    &:hover {
                        text-decoration-color: #E30613;
                        background: none;
                        border-bottom: 1px solid #E30613;
                    }
                    &--active {
                        font-weight: 800;
                    }
                }
            }
        }
        &--two-col {
            @media screen and (min-width: $break-tabletSmall) {
                min-width: 400px;               
            }
            .nav__country-selector__region__countries {
                @media screen and (min-width: $break-tabletSmall) {
                    column-count: 2;
                }
            }
        }
    }
    &--active &__activator__carrot {
        svg {
            transform: rotate(180deg);
        }
    }
}
.nav__language-selector {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    text-align: left;
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #c9cacd;
    @media screen and (min-width: $break-tabletSmall) {
        justify-content: center;
        text-align: center;
        padding-bottom: 10px;
        margin-bottom: 20px; 
        padding-top: 20px;      
        align-items: center;    
    }
    &__current-country{
        &__wrapper{
            display: flex;
            justify-content: center;
            margin-right: 25px;
        }
        &--desc{
            margin-right: 5px;
        }
        &--text{
            display: flex;
        }
    }

    &__languages {
        display: flex;
        z-index:1;
       
        &__language {
            border: none;

            &__link {
                // color: $gray;
                border: none;
                width: 100%;
                display: block;
                padding: 0 5px;
                font-weight: normal;
                white-space: nowrap;
                border-right: 1px solid #c9cacd;
                &:first-child {
                    padding-left: 0;
                }
                
                &:last-child {
                    border-right: none;
                }
    
                &:hover{
                    background: transparent;
                    color: #000;
                    text-decoration: underline;
                    border: none;
                }

                &--active{
                    text-decoration: underline;
                    font-weight: bold;
                }
            }
        }
    }

    &--active{
        .nav__language-selector{
            &__carrot{
                transform: rotate(180deg);
            }
        }
    }
}

