#masthead {
	background: rgba(#fff,.15);
	margin-bottom: 26px;
	&:after {
		content: "";
		clear: both;
		display: table;
	}
	.inner {
		margin-bottom: 10px;
		padding-top: 17px;
		padding-bottom: 12px;
		position: relative;
		@media screen and (min-width: $break-tabletSmall) {
			margin-bottom: 43px;
		}
		@media screen and (min-width: $break-desktopSmall) {
			margin-bottom: 62px;
			padding-bottom: 20px;
		}
		&:after {
			content: '';
			display: block;
			width: calc(100% - 50px);
			max-width: 1450px;
			height: 1px; 
			background-color: $color-border;
			margin-top: 20px;
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%,0);
			@media screen and (min-width: 1800px) {
				width: calc(100vw - 10%);
				max-width: 100%;
			}
		}
	}
	.container {
		&:after {
			content: "";
			clear: both;
			display: table;
		}
		.nav__country-selector {	
			@media screen and (min-width: $break-desktopSmall) {
				padding-top: 10px;					
			}
			&__activator {
				margin-left: auto;
			}
			&__aaas {
				margin-top: 20px;
			}
			.nav__country-selector__regions {
				align-self: flex-start;
				@media screen and (max-width: $break-tabletSmall - 1px) {
					width: 100%;					
				}
			}
		}
	}
	.logo {
		display: block;
		width: 80px;
		border: 0;
		float: left;
		z-index: 2;
		position: relative;
		@media screen and (min-width: $break-desktopSmall) {
			width: 133px;
		}
		&:hover,
		&:active {
			border: 0;
			background: transparent;
		}
	}
}