#colophon {
	background-color: #000;
	color: $color-grey-8;
	padding-top: 30px;
	padding-bottom: 30px;
	.logo {
		display: inline-block;
		border-bottom: none;
		&:hover {
			text-decoration: none;
			border-bottom: none;
		}
	}
	.logo-white {
		display: block;
		width: 82px;
		height: 36px;
		margin-bottom: 27px;
	}
	a {
		color: $color-grey-8;
		text-decoration: none;
		border-bottom: 0;
		font-weight: normal;
		&:hover,
		&:active {
			color: $color-red;
			background: transparent;
			border-bottom: 1px solid #fff;
		}
	}
	ul {
		list-style: none;
		padding-left: 0;
		li {
			display: inline-block;
			&:after {
				content: '|';
				margin-left: 3px;
				margin-right: 5px;
				display: inline-block;
				font-size: 18px;
			}
			&:last-child:after {
				display: none;
			}
		}
	}
	.content {
		@include clearfix();
		p {
			margin-bottom: 5px;	
			@media screen and (min-width: $break-tabletSmall) {
				max-width: 185px;
				float: right;
				padding-top: 5px;
				margin-right: 50px;
				margin-bottom: 0;
			}
			@media screen and (min-width: $break-desktopSmall) {
				max-width: 400px;
				margin-right: 0;
				text-align: right;
			}
		}
		nav {
			margin-bottom: 0;
			@media screen and (min-width: $break-tabletSmall) {
				max-width: calc(100% - 225px);
				padding-right: 50px;
			}
			@media screen and (min-width: $break-desktopSmall) {
				max-width: calc(100% - 400px);
			}
			ul {
				margin-bottom: 0;
			}
		}
	}
}
#preview-modal {
	overflow: hidden;
}
#update-notice {
	position: fixed;
	left: 0;
	bottom: 0;
	background-color: $color-green;
	color: #fff;
	text-align: center;
	width: 100%;	
	padding: 10px 20px;
	transition: opacity 0.3s;
	display: none;
	h2 {
		@include fontsize(20px);
		color: inherit;
		font-weight: normal;
		.check {
			width: 18px;
			height: 14px;
			margin-left: 10px;
		}
	}
}

#nested {
	display: none;
}