.container {
	width: 100%;
	max-width: 1500px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	@media screen and (min-width: $break-desktopSmall) {
		padding-left: 25px;
		padding-right: 25px;
	}
}
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: polygon(0px 0px, 0px 0px,0px 0px, 0px 0px);
	position: absolute !important;
	white-space: nowrap;
	height: 1px;
	width: 1px;
	overflow: hidden;
}
.right-chevron,
.left-chevron {
	width: 10px;
	height: 16px;
}
.required {
	color: $color-red;
}
#obfuscate {
	@include clearButton();
	cursor: pointer;
	line-height: 0;
	width: 45px;
	height: 45px;
	.eye-show {
		width: 18px;
		height: 12px;	
		display: inline-block;
		vertical-align: middle;
		position: relative;
		top: -1px;
	}
	.eye-hide {
		display: inline-block;
		vertical-align: middle;
		display: none;
		width: 18px;
		height: 14px;
		color: $color-grey-5;
	}
	&[aria-pressed="true"] {
		.eye-show { 
			display: none;
		}
		.eye-hide {
			display: inline-block;
		}
	}
	&[aria-pressed="false"] {
		.eye-hide {
			display: none;
		}
	}
}
.toggle {
	@include clearButton();
	width: 45px;
	height: 45px;
	cursor: pointer;
	&[aria-expanded="true"] .plus-minus:after {
		transform: translate(-50%,-50%) rotate(0deg);
	}
	.plus-minus {
		position: relative;
		display: block;
		&:before {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%);
			display: block;
			content: '';
			width: 19px;
			height: 4px;
			border-radius: 2px;	
			background-color: $color-red; 
		}
		&:after {
			position: absolute;
			left: 50%;
			top: 50%;
			transition: transform 0.3s ease-out;
			transform: translate(-50%,-50%) rotate(-90deg);
			display: block;
			content: '';
			width: 19px;
			height: 4px;
			border-radius: 2px;
			background-color: $color-red;
		}
	}	
}
.preview {
	@include clearButton();
	@include fontsize(16px);
	font-weight: 600;
	text-decoration: none;
	color: #000;
	cursor: pointer;
	border-bottom: 1px solid transparent;
	&:hover,
	&:active {
		span.label {
			border-bottom: 1px solid $color-red;
		}
	}
	svg {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		margin-left: 4px;
		fill: $color-red;
		@media screen and (min-width: $break-desktopSmall) {
			top: -2px;
		}
	}
}
.skip-link:focus {
	-webkit-clip-path: unset;
	clip-path: unset;
	position: relative!important;
	text-align: center
}
.skip-link:focus span {
	display: block
}
.hidden,
[hidden] {
	display: none!important;
}
#pre-modal {
	position: fixed; 
	bottom: 110px;
	right: 0;
	background-color: #efefef;
	box-shadow: 0 3px 6px rgba(#000,.16);
	padding: 15px 25px;
	font-size: 12px;
	ul {
		padding-left: 0;
		margin-bottom: 0;
	}
	li {
		list-style: none;
		padding-left: 0;
	}
}