button {
	font-family: $font-global;
}
.checkbox {
	input[type=checkbox] {
		@include element-invisible();
	}
	input[type=checkbox] + label:after {
		content: none;
	}
	input[type=checkbox]:checked + label:after {
		content: "";
	}
	input[type=checkbox]:checked + label:before {
		border-color: $color-red;
	}
	input[type=checkbox]:focus + label {
		outline: -webkit-focus-ring-color auto 1px;
	}
	label {
		@include fontsize(16px);
		position: relative;
		display: inline-block;
		cursor: pointer;
		padding-left: 46px;
		font-weight: 600;	
		margin-bottom: 8px;
		min-height: 44px;
		padding-top: 3px;
		@media screen and (min-width: $break-tabletSmall) {
			@include fontsize(20px);
		}
		&:hover,
		&:focus {
			&:before {
				border-color: $color-red;
			}
		}
		&:before,
		&:after {
			position: absolute;
			display: inline-block; 
			content: "";
		}
		&:before {
			width: 32px;
			height: 32px;
			border: solid 1px $color-grey-4;
			border-radius: 2px;
			top: 3px;
			left: 0;
			background-color: #fff;
		}
		&:after {
			height: 8px;
			width: 17px;
			border-left: 2px solid $color-red;;
			border-bottom: 2px solid $color-red;
			transform: rotate(-50deg);
			left: 8px;
			top: 13px;
		}
	}
	p {
		padding-left: 37px;
		@media screen and (max-width: $break-tabletSmall - 1px) {
			margin-bottom: 12px !important;
		}
		@media screen and (min-width: $break-tabletSmall) {
			margin-bottom: 11px !important;
		}
	}
	.checkbox {
		margin-left: 38px;
		input[type=checkbox]+label:before {
			top: 1px;
		}
		input[type=checkbox]+label:after {
			top: 10px;
		}
		label {
			@include fontsize(16px);
		}
	}
}
input[type="text"],
input[type="email"] {
	@include fontsize(16px);
	min-height: 48px;
	padding: 8px 15px;
	color: $color-grey-12;
	width: 100%;
	border: 2px solid $color-grey-13;
}
select {
	@include fontsize(16px);
	min-height: 48px;
	padding: 8px 41px 8px 15px;
	color: $color-grey-12;
	width: 100%;
	border: 2px solid $color-grey-13;
	appearance: none;
	cursor: pointer;
	background: right 14px top 17px no-repeat url('../images/down-arrow.png') #fff;
	background-size: 19px auto;
}
select::-ms-expand {
    display: none;
}
button[type="submit"],
input[type="submit"] {
	@include fontsize(16px);
	text-align: center;
	width: 100%;
	padding: 24px 25px;
	border: none;
	cursor: pointer;
	transition: 0.3s;
	min-height: 46px;
	background-color: $color-red;
	color: #fff;
	max-width: 278px;
	&:hover {
		background-color: #000;
	}
}
.validation-error {
	input:not([type="radio"]):not([type="checkbox"]):not([type="submit"]):not([type="button"]):not([type="image"]):not([type="file"]),
	select,
	textarea {
		border-color: $color-red;
	}
	.error-message {
		display: block;
		color: $color-red;
		font-weight: bold;
		margin-top: 7px;
	}
	::-webkit-input-placeholder {
	  color: $color-red;
	  font-weight: bold;
	}
	::-moz-placeholder {
	  color: $color-red;
	  font-weight: bold;
	}
	:-ms-input-placeholder {
	  color: $color-red;
	  font-weight: bold;
	}
	:-moz-placeholder {
	  color: $color-red;
	  font-weight: bold;
	}
}
form {
	max-width: 777px;
	.row {
		@media screen and (min-width: $break-tabletSmall) {
			display: flex;
			flex-direction: row;
			flex-wrap: nowrap;
		}
		.field {
			margin-bottom: 20px;
			@media screen and (min-width: $break-tabletSmall) {
				width: calc(50% - 8px);
				&:nth-of-type(1) {
					margin-right: 8px;
				}
				&:nth-of-type(2) {
					margin-left: 8px;
				}
			}
		}
	}
}
.g-recaptcha {
	margin-top: 5px;
	margin-bottom: 30px;
}
